html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  list-style: none;
  text-decoration: none;
}

body {
  color: #222726;
  font-family: "Rubik", sans-serif;
  font-size: 1.6rem;
}

[class$=container],
.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3, h4, h5, h6, p, a, input, textarea, ul {
  margin: 0;
  padding: 0;
}

.seccion {
  padding: 8rem 0;
}

ul {
  list-style-type: none;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
}

.btn {
  background-color: #FFFFFF;
  padding: 2rem 2rem;
  color: #222726;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media screen and (max-width: 768px) {
  .btn {
    width: auto;
  }
}
.btn:hover {
  background-color: #e6e6e6;
}

.boton-negro {
  background-color: #222726;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media screen and (max-width: 768px) {
  .boton-negro {
    width: auto;
  }
}
.boton-negro:hover {
  background-color: #0a0c0b;
}

.wrapper {
  display: grid;
  grid-template-columns: 1fr 4fr;
  background-color: rgb(55, 59, 51);
}
@media screen and (max-width: 480px) {
  .wrapper {
    grid-template-columns: 1fr;
    padding: 0;
  }
}

.header-mobile {
  display: none;
  position: sticky;
  top: 0;
  background-color: rgb(55, 59, 51);
  z-index: 5;
}
@media screen and (max-width: 480px) {
  .header-mobile {
    display: flex;
    padding: 1rem;
    margin-top: 0;
    justify-content: space-between;
    align-items: center;
  }
}

.open-menu,
.close-menu {
  background-color: transparent;
  color: #ececec;
  border: none;
  font-size: 3rem;
  cursor: pointer;
}

.close-menu {
  display: none;
}
@media screen and (max-width: 480px) {
  .close-menu {
    display: block;
    position: absolute;
    top: 2rem;
    right: 3rem;
  }
}

.aside {
  padding: 2rem;
  padding-right: 0;
  color: #ececec;
  position: sticky;
  top: 0;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media screen and (max-width: 480px) {
  .aside {
    position: fixed;
    z-index: 10;
    background-color: rgb(55, 59, 51);
    left: 0;
    box-shadow: 0 0 0 100vmax rgba(0, 0, 0, 0.75);
    transform: translateX(-110%);
    opacity: 0;
    visibility: hidden;
    transition: all 200ms ease-in-out;
  }
}
.aside__link-logo {
  display: inline-block;
  max-width: max-content;
}
.aside__logo {
  max-width: max-content;
  font-weight: 400;
  font-size: 2.5rem;
  color: #ececec;
}
.aside__footer {
  color: #848c7b;
  font-size: 1.2rem;
}

.aside-visible {
  transform: translateX(0%);
  opacity: 1;
  visibility: visible;
  transition: all 200ms ease-in-out;
}

.bi-hand-index-thumb,
.bi-hand-index-thumb-fill {
  transform: rotate(90deg);
}

.nav__wrapper {
  display: flex;
  flex-direction: column;
  row-gap: 1.2rem;
}
.nav__menu {
  background-color: #494e44; /* ant transparent */
  border: none;
  color: #ececec;
  font-size: 1.2rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 1rem;
  font-weight: 600;
  padding: 1rem;
  font-size: 1.3rem;
  border-top-left-radius: 100vmax;
  border-bottom-left-radius: 100vmax;
  position: relative;
  width: 100%; /* ext */
}
.nav__menu.active {
  background-color: #ececec;
  color: rgb(55, 59, 51);
  width: 100%;
}
.nav__menu.active::before {
  content: "";
  position: absolute;
  width: 1rem;
  height: 2rem;
  bottom: 100%;
  right: 0;
  background-color: transparent;
  border-bottom-right-radius: 1rem;
  box-shadow: 0 1rem 0 #ececec;
}
.nav__menu.active::after {
  content: "";
  position: absolute;
  width: 1rem;
  height: 2rem;
  top: 100%;
  right: 0;
  background-color: transparent;
  border-top-right-radius: 1rem;
  box-shadow: 0 -1rem 0 #ececec;
}
@media screen and (max-width: 480px) {
  .nav__menu.active::after, .nav__menu.active::before {
    display: none;
  }
}
.nav__cart {
  margin-top: 2.5rem;
}

.grid {
  background-color: #ececec;
  margin: 1rem;
  margin-left: 0;
  border-radius: 2rem;
  padding: 3rem;
}
@media screen and (max-width: 480px) {
  .grid {
    margin: 1rem;
    margin-top: 0.5rem;
    padding: 2rem;
  }
}
.grid__header {
  color: rgb(55, 59, 51);
  margin-bottom: 2rem;
  font-weight: 700;
  font-size: 2.5rem;
}
.grid__products {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.2rem;
}
@media screen and (max-width: 1024px) {
  .grid__products {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media screen and (max-width: 768px) {
  .grid__products {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 480px) {
  .grid__products {
    grid-template-columns: repeat(2, 1fr);
  }
}

.product {
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
  border-radius: 1.5rem;
  border: 1px solid #808080;
  overflow: hidden;
}
.product__img {
  max-width: 100%;
  border-radius: 1rem;
}
.product__details {
  background-color: rgb(55, 59, 51);
  color: #ececec;
  padding: 0.5rem 1rem;
  /* border-radius: 0 0 1rem 1rem; */
  margin-top: -2rem;
  position: relative;
  display: flex;
  flex-direction: column;
  row-gap: 0.5rem;
}
.product__header {
  font-size: 1.5rem;
}
.product__add {
  border: 2px solid #ececec;
  background-color: #ececec;
  color: rgb(55, 59, 51);
  padding: 0.4rem;
  text-transform: uppercase;
  border-radius: 100vmax;
  cursor: pointer;
  transition: all 200ms;
}
.product__add:hover {
  background-color: rgb(55, 59, 51);
  color: #ececec;
}